@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;600&display=swap);
/*Global Styles*/

* {
  font-family: "Poppins", sans-serif;
}
.App {
  text-align: center;
}

.App-header {
  background: #7aa1d2; /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #7aa1d2, #dbd4b4, #7aa1d2);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
}

input,
button,
select,
optgroup,
textarea {
  font-size: medium !important;
  font-size: initial !important;
}

:focus {
  outline: none !important;
}

/*Buttons*/

.button-wrap {
  display: block !important;
}

.top-button {
  width: 6em;
  border-radius: 10px;
  border: 2px #7ca2d2;
  border-style: solid;
  padding: 0.2em;
  background: #ffff;
  font-weight: bold;
  transition: all 0.2s;
  margin-right: 4px;
  margin-top: 0.5em;
}

.top-button:hover {
  background: #5a8bcd;
}

/* Pokémon Cards */

.container {
  width: 80%;
  margin: 2rem auto;
  display: flex;
  justify-content: center;
}

.card-poke {
  width: 330px;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 10px 10px 50px rgba(0, 0, 0, 0.2);
  background: #fffd24;
}

.card-poke :hover img {
  transform: scale(1.2);
}

.name {
  color: #000;
  font-weight: bold;
  text-align: left;
  font-size: 1.5rem !important;
}

.type {
  text-align: right;
  font-weight: 400;
  color: #000;
  font-size: 1rem !important;
  margin-top: 0.8em;
}
.img {
  text-align: center;
  z-index: 1;
  align-content: center;
  justify-content: center;
}

.img img {
  -webkit-filter: drop-shadow(0 50px 27px rgba(0, 0, 0, 0.4));
          filter: drop-shadow(0 50px 27px rgba(0, 0, 0, 0.4));
  transition: all 0.5s ease;
  position: inherit;
  margin: 28px auto;
  padding-top: 1em;
}
.foot-card {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.foot-card button {
  width: 75%;
  border-radius: 10px;
  border: none;
  padding: 1.2em 0;
  background: white;
  font-weight: bold;
  transition: all 0.2s;
}

.foot-card button:hover {
  background: #5a8bcd;
}

/*Pokemon Card Columns : Overwriting Bootstrap's default styles*/

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px !important;
  margin-left: -20px !important;
}

@media (min-width: 1200px) {
  .row {
    margin-right: -15px !important;
    margin-left: 15px !important;
  }
}

@media (min-width: 351px) and (max-width: 767px) {
  .row {
    margin-right: 0px !important;
    margin-left: 3em !important;
  }
}


/*Modal : Overwriting Bootstrap's default styles*/
.modal-body {
    color: rgb(142, 142, 143);
  }
  
  ul {
    list-style-type: none;
  }
  
  .btn-secondary {
    background-color: #3563ad !important;
    border-color: #3563ad !important;
  }
/*Navbar*/

.nav-wrap {
  width: 100%;
  position: -webkit-sticky !important;
  position: sticky !important;
}

.nav-poke {
  background-color: rgba(255, 255, 255, 0.5) !important;
}

/*Search bar*/
.search-poke-bar {
  border-radius: 5px;
  border: #ffff;
}

.search-poke-bar::-webkit-input-placeholder {
  /* Edge */
  color: #5a8bcd;
}

.search-poke-bar:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #5a8bcd;
}

.search-poke-bar::-ms-input-placeholder {
  color: #5a8bcd;
}

.search-poke-bar::placeholder {
  color: #5a8bcd;
}

